export default "html {\n  height: 100%;\n}\n\n.noselect {\n  -webkit-touch-callout: none;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n\n.company-table-head {\n  border: none;\n  background-color: #bc044e;\n  /* color: #bc044e */\n  color: #eee;\n}\n\n.table {\n  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.2);\n}\n\nbutton.btn {\n  margin: 2px;\n}\n\n.refresh {\n  background-color: transparent;\n  border: 0;\n  color: white;\n  /* margin-left: 80px */\n  float: right;\n  color: inherit;\n  margin-bottom: -10px;\n}\n\n.icon-button:hover {\n  background-color: #b5b5b5;\n  border-radius: 2px;\n  color: white;\n}\n\n.drag-handle {\n  cursor: -webkit-grab;\n  cursor: grab;\n}\n\n.card {\n  margin-bottom: 10px;\n}\n\n.icon-small {\n  font-size: 1em;\n}\n\nbody {\n  font-size: 0.875rem;\n}\n\n/* Sidebar */\n\n.sidebar {\n  position: fixed;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  z-index: 100;\n  padding: 48px 0 0;\n  box-shadow: inset -1px 0 0 rgba(0, 0, 0, 0.1);\n  width: 220px;\n  max-width: 220px;\n}\n\n.sidebar-sticky {\n  position: relative;\n  top: 0;\n  height: calc(100vh - 48px);\n  padding-top: 0.5rem;\n  overflow-x: hidden;\n  overflow-y: auto;\n}\n\n@supports ((position: -webkit-sticky) or (position: sticky)) {\n  .sidebar-sticky {\n    position: -webkit-sticky;\n    position: sticky;\n  }\n}\n\n.sidebar .nav-link {\n  font-weight: 500;\n  color: #333;\n}\n\n.sidebar .nav-link.active {\n  color: #bc044e;\n  background-color: #cbcbcb;\n}\n\n.sidebar .nav-link:hover {\n  background-color: #dddddd;\n}\n\n.sidebar .nav-link:hover .active,\n.sidebar .nav-link.active .active {\n  color: inherit;\n}\n\n.sidebar-heading {\n  font-size: 0.75rem;\n  text-transform: uppercase;\n}\n\n/* Content */\n\n[role=main] {\n  padding-top: 48px;\n}\n\n/* Navbar */\n\n/* .navbar-brand\n * * padding-top: .75rem\n * * padding-bottom: .75rem\n * * font-size: 1rem\n * * margin: 0\n * * margin-top: -10px\n * * background-color: rgba(0, 0, 0, .75)\n * * box-shadow: inset -1px 0 0 rgba(0, 0, 0, .25) */\n\n.navbar-brand a {\n  color: white;\n}\n\n.navbar .form-control {\n  padding: 0.75rem 1rem;\n  border-width: 0;\n  border-radius: 0;\n}\n\n.form-control-dark {\n  color: #fff;\n  background-color: rgba(255, 255, 255, 0.1);\n  border-color: rgba(255, 255, 255, 0.1);\n}\n\n.form-control-dark:focus {\n  border-color: transparent;\n  box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.25);\n}\n\n/* Utilities */\n\n.material-icons {\n  float: left;\n  margin-right: 20px;\n}\n\n.main-view {\n  /* margin-left: 220px */\n  width: calc(100% - 220px);\n  min-width: calc(100% - 220px);\n  max-width: calc(100% - 220px);\n  float: left;\n}\n/*# sourceMappingURL=data:application/json;base64, */"